.layout {
  background-color: white; }
  .layout .layout-container {
    width: 80%;
    margin: auto; }
    .layout .layout-container .error-message {
      margin: 10px 0px 0px 0px; }
    .layout .layout-container .login-content {
      display: flex;
      margin-top: 5%; }
      .layout .layout-container .login-content .login-card {
        display: table;
        float: none; }
        .layout .layout-container .login-content .login-card .left-grid {
          background-color: #f6e6a3;
          width: 50%;
          padding: 60px;
          display: table-cell;
          float: none;
          vertical-align: middle; }
          .layout .layout-container .login-content .login-card .left-grid .left-grid-text {
            line-height: 2; }
        .layout .layout-container .login-content .login-card .right-grid {
          padding: 60px;
          display: table-cell;
          width: 50%; }
          .layout .layout-container .login-content .login-card .right-grid .login-form {
            display: flex;
            flex-direction: column; }
            .layout .layout-container .login-content .login-card .right-grid .login-form p a {
              text-decoration: underline; }
            .layout .layout-container .login-content .login-card .right-grid .login-form .login-form-title {
              text-align: center; }

@media screen and (max-width: 650px) {
  .layout .layout-container {
    width: 80%;
    margin: auto; }
    .layout .layout-container .login-content .login-card {
      padding: 5%; }
      .layout .layout-container .login-content .login-card .left-grid {
        display: table;
        width: 100%;
        display: table; }
      .layout .layout-container .login-content .login-card .right-grid {
        display: unset;
        width: 100%; } }

.main-page .main-page-content {
  margin: auto;
  padding: 2%; }
  .main-page .main-page-content .main-page-content-col .title {
    cursor: pointer; }
  .main-page .main-page-content .main-page-content-col img {
    width: 100%; }
  .main-page .main-page-content .news-list {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px; }
    .main-page .main-page-content .news-list .full-news-list {
      width: 70%;
      margin: auto; }
    .main-page .main-page-content .news-list .business-news-list {
      background-color: #2f54eb;
      width: 25%; }
      .main-page .main-page-content .news-list .business-news-list img {
        width: 40%; }

@media screen and (max-width: 850px) {
  .main-page {
    width: 100vh; }
  .main-page-content-col {
    margin: calc(100vh -4em); } }

.profile-page {
  width: 90%;
  margin: auto;
  padding: 3%; }
  .profile-page img {
    width: 100%; }
  .profile-page .profile-card {
    padding: 20px; }

.story-page {
  width: 70%;
  margin: auto; }
  .story-page img {
    height: 25%;
    width: 100%;
    padding: 15px; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.ant-btn-primary:active,
.ant-btn-primary.active {
  background-color: #2f54eb;
  border-color: #2f54eb; }

.ant-card-body {
  padding: 0px;
  zoom: 1; }

