.layout {
  background-color: white;
  .layout-container {
    width: 80%;
    margin: auto;
    .error-message {
      margin: 10px 0px 0px 0px;
    }
    .login-content {
      display: flex;
      margin-top: 5%;
      .login-card {
        display: table;
        float: none;
        .left-grid {
          background-color: #f6e6a3;
          width: 50%;
          padding: 60px;
          display: table-cell;
          float: none;
          vertical-align: middle;
          .left-grid-text {
            line-height: 2;
          }
        }
        .right-grid {
          padding: 60px;
          display: table-cell;
          width: 50%;
          .login-form {
            display: flex;
            flex-direction: column;
            p {
              a {
                text-decoration: underline;
              }
            }
            .login-form-title {
              text-align: center;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .layout {
    .layout-container {
      width: 80%;
      margin: auto;
      .login-content {
        .login-card {
          padding: 5%;
          .left-grid {
            display: table;
            width: 100%;
            display: table;
          }
          .right-grid {
            display: unset;
            width: 100%;

          }
        }
      }
    }
  }
}
